@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-container {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 30px;
  width: 100vw;
  background-color: #ffffff;
}

.logo {
  height: 25px;
}
.tag-container {
  padding: 2px 15px;
  border: #eeeeee 1px solid;
  border-radius: 3px;
  margin-right: 5px;
  margin-bottom: 10px;
  font-size: 12px;
  cursor: pointer;
}

.tag-container:hover {
  background-color: #eeeeee
}
.product-overview-container {
  background-color: #ffffff;
  width: 300px;
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  align-items: center;
  padding-top: 25px;
  box-shadow: 0px 8px 6px rgba(0,0,0,0.1);
}

.product-info-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.product-tags-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 15px 20px;
  border-top: #eeeeee 1px solid;
  border-bottom: #eeeeee 1px solid;
  margin-bottom: 30px;
}

.product-pages-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 85%;
}

.product-pages-container > div {
  margin-bottom: 30px;
  text-transform: uppercase;
}

.product-image {
  width: 60%;
  margin-bottom: 12px;
}

.product-title {
  font-size: 17px;
  margin-bottom: 6px;
  font-weight: bold;
}

.product-subtitle {
  font-size: 12px;
  color: #aaaaaa;
  width: 70%;
}

.page-link {
  color: #949393;
  cursor: pointer;
}

.selected-page {
  font-weight: bold;
  cursor: pointer;
  color: #000000
}
.product-table-container {
  flex-grow: 1;
  background-color: #ffffff;
  box-shadow: 0px 8px 6px rgba(0,0,0,0.1);
}

table {
  table-layout: auto;
  width: 100%;
  border-collapse: collapse;
}

tr > th {
  text-transform: uppercase;
  padding: 20px;
  position: relative;
  cursor: pointer;
  font-size: 12px;
  border-bottom: #eeeeee 1px solid;
  height: 25px;
}

tr > td {
  padding: 15px;
  color: #a3a3a3;
  border-bottom: #eeeeee 1px solid;
}

.sort-arrow {
  position: absolute;
  right: 40px;
  top: 29%;
  transform: rotate(180deg);
  color: #cacaca;
  font-size: 15px;
}

.sort-arrow-reverse {
  position: absolute;
  right: 40px;
  top: 38%;
  color: #cacaca;
  font-size: 15px;
}

.selected-column {
  background-color: #e6f5ff;
}
.App {
  text-align: center;
  background-color: #f7f7f7;
  font-family: 'Lato', sans-serif;
}

.product-content-container {
  display: flex;
  padding: 70px 20px;
}

